@import "src/common/styles/sass/variables.sass";
@import "src/common/styles/sass/mixins.sass";
.title{
    ///*border: 1px solid;*/
    ////margin-bottom: 20px;
    margin: 0 0 30px;
    @include text($titleFontFamily,36px, 1.2, 300);
    //color: $primaryColor;
    text-transform: capitalize;
    letter-spacing: 5px;
    position: relative;
    text-align: center;
    .title h2{
        margin: 0;
    }
    &::before{
        content:"";
        position: absolute;
        background:$bgColor;
        width: 55px;
        height: 1px;
        bottom: -30px;
        left: 50%;
        transform: translate(-50%,50%);
    }
}